.timeline {
  position: relative;
  margin: 50px 0;
  background-size: cover;
}
.timline .head-style{
    width: 100px;
    margin: auto;
}
.time-line {
  position: relative;
  margin: 4rem 0 2rem 0;
}
.timline .time-line::before {
  content: "";
  position: absolute;
  width: 0.4rem;
  height: 100%;
  background: tomato;
  left: 50%;
  top:0;
  bottom: 0;
}
.time-line ul {
  margin: 0;
  position: relative;
}
.time-line ul li {
  list-style: none;
  background: #fafaf4;
  width: 50%;
  padding: 2rem;
  border-radius: 3px;
  box-shadow: 0 0px 2px 1px rgba(138, 137, 137, 0.5);
  position: relative;
}
.time-line ul li:nth-child(odd) {
  float: left;
  text-align: left;
  clear: both;
}
.time-line ul li:nth-child(even) {
  float: right;
  text-align: left;
  clear: both;
}

.time-line ul li p {
  margin: 10px;
}
.timeline-content {
  padding: 10px 20px 10px 20px;
  border: 1px solid #eee;
  text-align: justify;
  background-color: white;
}
.timespan {
  text-align: center;
  border-radius: 4px;
  padding: 0.2rem 1rem;
  background:var(--p-dark);
  color: white;
  box-shadow: 0 0 3px var(--secondary);
}

@media (max-width: 1000px) {
  .time-line ul li {
    width: 100%;
  margin: 2rem auto;

  }
  .time-line ul li:nth-child(odd) {
    float: none;
    text-align: left;
    clear: none;
    /* border-right: 2px solid --primary; */
  }
  .time-line ul li:nth-child(even) {
    float: none;
    text-align: left;
    clear: none;
    /* border-left: 2px solid --primary; */
  }
  .timespan {
    position: relative;
  }
  .time1 {
    left: 0%;
    top: 35%;
  }
  .time {
    top: 30%;
    right: 00%;
  }
}

